import React, { useState, useEffect } from 'react';
import EmptyPoster from '../../img/empty_poster.jpg';
import EmptyActor from '../../img/empty_actor.jpg';
import { Center, SearchBar, Title, ItemWrapper, Item, SubTitle } from '../styles/searchstyles';

function Search() {
  const [error, setError] = useState(null);
  const [items, setItems] = useState([]);
  const [query, setQuery] = useState('');
  const api_key = '58666352553834b5820ef436680fb2e5';

  const handleChange = (e) => {
    setQuery(e.target.value);
  };
  const havePhoto = ({ media_type, poster_path, profile_path, name, title }) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        return <img alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
      } else {
        return <img alt={title} src={EmptyPoster} />;
      }
    } else if (media_type === 'person') {
      if (profile_path) {
        return <img alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
      } else {
        return <img alt={name} src={EmptyActor} />;
      }
    }
  };

  useEffect(() => {
    if (query) {
      fetch(`https://api.themoviedb.org/3/search/multi?api_key=${api_key}&language=pl-PL&query=${query}&page=1&include_adult=false`)
        .then((res) => res.json())
        .then(
          (result) => {
            setItems(result.results);
          },
          (error) => {
            setError(error);
          }
        );
    }
  }, [query]);
  if (error) console.log(error);
  return (
    <Center>
      <Title>InfCinema</Title>
      <SubTitle>Wyszukaj Film, Serial lub Aktora</SubTitle>
      {/* <form autoComplete='off' onSubmit={(e) => e.preventDefault()}> */}
      <SearchBar autoComplete='off' id='standard-basic' label='Wyszukaj' onChange={handleChange} />
      {/* </form> */}
      <ItemWrapper>
        {items &&
          items.slice(0, 24).map((item, i) => {
            if (item.media_type === 'movie') {
              return (
                <Item to={`/infcinema/details/${item.media_type}/${item.id}`} key={i}>
                  {havePhoto(item)}
                  {item.title && <div className='tit'>{item.title}</div>}
                </Item>
              );
            } else if (item.media_type === 'tv') {
              return (
                <Item to={`/infcinema/details/${item.media_type}/${item.id}`} key={i}>
                  {havePhoto(item)}
                  {item.name && <div className='tit'>{item.name}</div>}
                </Item>
              );
            } else if (item.media_type === 'person') {
              return (
                <Item to={`/infcinema/details/${item.media_type}/${item.id}`} key={i}>
                  {havePhoto(item)}
                  {item.name && <div className='tit'>{item.name}</div>}
                </Item>
              );
            } else {
              return 0;
            }
          })}
      </ItemWrapper>
    </Center>
  );
}

export default Search;
