import React from 'react';
import { Link } from 'react-router-dom';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
const Nav = (props) => {
  return (
    <AppBar
      position='static'
      style={{ backgroundColor: '#042745', flexDirection: 'row', justifyContent: 'flex-start', alignItems: 'center', marginBottom: '30px' }}>
      <Toolbar justify='center'>
        <Link to='/infcinema' style={{ color: 'white', textDecoration: 'none' }}>
          <Typography variant='h6'>
            InfCinema <span style={{ color: 'gray' }}>alfa 1.0</span>
          </Typography>
        </Link>
      </Toolbar>
    </AppBar>
  );
};

export default Nav;
