import styled from 'styled-components';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
// import Modal from '@material-ui/core/Modal';
import Dialog from '@material-ui/core/Dialog';

export const SearchBar = styled.input`
  width: calc(100% - 40px);
  @media (min-width: 1200px) {
    width: 1200px;
    font-size: 40px !important;
    padding: 15px;
  }
  padding: 5px;
  font-size: 30px !important;
  border: 5px solid #153856;
  color: #042745;
  margin-bottom: 20px;
`;
export const Center = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  /* position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); */
  height: 100%;
  @media (min-width: 1200px) {
    min-height: calc(100vh - 273px);
  }
  min-height: calc(100vh - 265px);
  width: 100%;
`;
export const Title = styled.div`
  margin-top: 60px !important;
  color: #153856;
  @media (min-width: 1200px) {
    margin-bottom: 10px;
    font-size: 60px;
  }
  font-size: 40px;
  margin-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
`;
export const SubTitle = styled.div`
  color: #153856;
  @media (min-width: 1200px) {
    margin-bottom: 30px;
    font-size: 23px;
  }
  font-size: 15px;
  margin-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
`;
export const ItemWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  @media (min-width: 1200px) {
    padding: 15px;
    justify-content: flex-start;
  }
  justify-content: center;
  padding: 5px;
`;
export const Item = styled(Link)`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 10px;
  width: 185px;
  height: 278px;
  position: relative;
  opacity: 0.95;
  overflow: hidden;
  @media (max-width: 420px) {
    width: 85%;
    height: auto;
    img {
      width: 100%;
      height: auto;
    }
  }
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
  img {
    transition: transform 0.3s ease-in-out;
  }
  &:hover {
    opacity: 1;
    img {
      transform: scale(1.1);
    }
  }
  .tit {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 10, 20, 0.9);
    color: #d7e5fd;
    width: 100%;
    font-size: 15px;
    padding: 10px;
    text-decoration: none;
    font-weight: lighter;
  }
  .tit2 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: white;
    color: black;
    width: 100%;
    font-size: 15px;
    padding: 10px;
    text-decoration: none;
    font-weight: light;
    .character {
      font-size: 12px;
      font-weight: lighter;
    }
  }
`;
export const TitleImg = styled.img`
  width: 100%;
  max-width: 450px;
`;
export const MovieInfo = styled.div`
  font-size: 20px;
  color: #555;
  font-weight: 300;
  span {
    font-weight: 400;
    color: #042745;
    font-size: 15px;
  }
  @media (min-width: 1200px) {
    font-size: 25px;
    span {
      font-size: 20px;
    }
  }
`;
export const PhotoContainer = styled(Grid)`
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 25px !important;
`;
export const InfoContainer = styled(Grid)`
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  padding: 25px !important;
  padding-bottom: 110px !important;
`;
export const GoBack = styled(Link)`
  position: absolute;
  top: 65px;
  right: 25px;
  @media (min-width: 768px) {
    top: 110px;
    right: 50px;
  }
  color: #042745;
`;
export const TitleItem = styled(Grid)`
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 25px !important;
  font-weight: lighter;
  font-size: 26px;
`;
export const Sec = styled(Grid)`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 100px;
  font-size: 40px;
  font-weight: lighter;
  margin: 50px 25px;
  margin-bottom: 20px;
`;
export const SeeMore = styled(Grid)`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: lighter;
  cursor: pointer;
  color: #042745;
`;
export const Posters = styled.img`
  width: 100%;
  cursor: pointer;
`;
export const PosterWrapper = styled(Grid)`
  padding: 20px;
  /* margin-bottom: 60px; */
`;
export const CenterTitle = styled(Grid)`
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: lighter;
  font-size: 30px;
`;
export const OutWrp = styled(Grid)`
  background-color: white;
  margin-bottom: 60px;
  padding-top: 60px;
  padding-bottom: 60px;
`;
export const PhotoGallery = styled(Dialog)`
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  img {
    max-height: 800px !important;
  }
  .MuiDialog-paperWidthXs {
    max-width: 1200px;
  }
`;
export const CloseButton = styled.div`
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 10px 10px 40px 40px;
  border-bottom-left-radius: 100%;
  background-color: white;
  transition: 0.2s ease-in-out all;
  &:hover {
    padding: 10px 10px 50px 50px;
  }
  cursor: pointer;
`;
export const OpenFull = styled.a`
  position: absolute;
  bottom: 20px;
  left: 15px;
  background-color: white;
  color: #000;
  text-decoration: none;
  border-radius: 15px;
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.75);
  /* transform: translate(-50%); */
  font-weight: bold;
  padding: 4px 10px;
  /* padding: 10px 10px 40px 40px;
  border-bottom-left-radius: 100%;
  background-color: white;
  transition: 0.2s ease-in-out all;
  &:hover {
    padding: 10px 10px 50px 50px;
  } */
  cursor: pointer;
`;
export const EpisodeWrapper = styled.div`
  display: flex;
  justify-content: flex-start !important;
  align-items: center;
  margin: 10px 0;
  background-color: white;
  width: 100% !important;
  flex-direction: column;
  @media (min-width: 600px) {
    flex-direction: row;
  }
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.1);
`;
export const EpisodeLeftPanel = styled.div`
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 20px;
`;
export const EpisodeTitle = styled.div`
  /* font-weight: bold; */
  span {
    color: #153856;
    font-weight: bold;
    font-size: 25px;
  }
  margin: 10px 0;
`;
export const EpisodeOverview = styled.div`
  margin: 10px 0;
  font-weight: lighter;
`;
export const EpisodeAirDate = styled.div`
  margin: 10px 0;
  color: gray;
`;
export const SeasonPoster = styled.div`
  position: static;
  @media (min-width: 960px) {
    position: fixed;
    top: 94px;
  }
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;
export const EpisodeImg = styled.img`
  width: 100% !important;
  @media (min-width: 600px) {
    width: 300px !important;
  }
`;
export const EpisodeImgWrapper = styled.div`
  width: 100% !important;
  @media (min-width: 600px) {
    width: 300px !important;
  }
`;
