import React, { useState, useEffect } from 'react';
import EmptyPoster from '../../img/empty_poster.jpg';
import EmptyActor from '../../img/empty_actor.jpg';
import { ItemWrapper, Item, TitleImg, MovieInfo, PhotoContainer, TitleItem, InfoContainer, GoBack, Sec, SeeMore } from '../styles/searchstyles';
import Grid from '@material-ui/core/Grid';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';

function Details(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [actor, setActor] = useState('');
  const [credits, setCredits] = useState('');
  const [moviesCount, setMoviesCount] = useState(18);
  const [tvCount, setTvCount] = useState(18);
  const api_key = '58666352553834b5820ef436680fb2e5';
  const person_id = props.match.params.id;

  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPoster} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActor} />;
      }
    }
  };
  const handleMore = (type) => {
    if (type === 'movies') {
      if (moviesCount === 1000) setMoviesCount(18);
      else setMoviesCount(1000);
    } else if (type === 'tv') {
      if (tvCount === 1000) setTvCount(18);
      else setTvCount(1000);
    }
  };
  useEffect(() => {
    fetch(`https://api.themoviedb.org/3/person/${person_id}?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setActor(result);
          setIsLoaded(true);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/person/${person_id}/combined_credits?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setCredits(result);
          setIsLoaded2(true);
        },
        (error) => {
          setIsLoaded2(true);
          setError(error);
        }
      );
    window.scrollTo(0, 0);
  }, []);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded | !isLoaded2) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        <Grid container spacing={0} style={{ postition: 'relative' }}>
          <GoBack>
            <ArrowBackIcon fontSize='large' onClick={() => props.history.goBack()} />
          </GoBack>
          <PhotoContainer item xs={12} md={6} lg={4}>
            {havePhoto(actor, 'big')}
            <TitleItem>{actor.name}</TitleItem>
          </PhotoContainer>
          <InfoContainer item xs={12} md={6} xl={8}>
            <MovieInfo>
              Data urodzenia: <span>{actor.birthday}</span>
            </MovieInfo>
            {actor.deathday && (
              <MovieInfo>
                Data śmierci: <span>{actor.deathday}</span>
              </MovieInfo>
            )}
            <MovieInfo>
              Wiek: <span>{actor.birthday && Math.abs(new Date(new Date() - new Date(actor.birthday)).getUTCFullYear() - 1970)}</span>
            </MovieInfo>
            <MovieInfo>
              Miejsce urodzenia: <span>{actor.place_of_birth}</span>
            </MovieInfo>
            <MovieInfo>
              Również {actor.gender === 2 ? 'znany' : 'znana'} jako: <span>{actor.also_known_as.join(', ')}</span>
            </MovieInfo>
          </InfoContainer>
        </Grid>
        <Sec>Filmy</Sec>
        <ItemWrapper>
          {credits.cast
            .filter((a) => a.media_type === 'movie')
            .sort((a, b) => {
              if (a.release_date > b.release_date) return -1;
              if (a.release_date < b.release_date) return 1;
              return 0;
            })
            .slice(0, moviesCount)
            .map((item, index) => (
              <Item to={`/infcinema/details/${item.media_type}/${item.id}`} key={index}>
                {havePhoto(item)}
                <div className='tit2'>
                  {item.title} {item.release_date && `(${item.release_date.slice(0, 4)})`}
                  <br />
                  <span className='character'>{item.character}</span>
                </div>
              </Item>
            ))}
        </ItemWrapper>
        {credits.cast.length > 18 && (
          <div onClick={() => handleMore('movies')}>{moviesCount > 20 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}</div>
        )}
        <Sec>Seriale</Sec>
        <ItemWrapper>
          {credits.cast
            .filter((a) => a.media_type === 'tv')
            .sort((a, b) => {
              if (a.first_air_date > b.first_air_date) return -1;
              if (a.first_air_date < b.first_air_date) return 1;
              return 0;
            })
            .slice(0, tvCount)
            .map((item, index) => {
              //   console.log(item.first_air_date);
              return (
                <Item to={`/infcinema/details/${item.media_type}/${item.id}`} key={index}>
                  {havePhoto(item)}
                  <div className='tit2'>
                    {item.name} {item.first_air_date && `(${item.first_air_date.slice(0, 4)})`}
                    <br />
                    <span className='character'>{item.character}</span>
                  </div>
                </Item>
              );
            })}
        </ItemWrapper>
        {credits.cast.length > 18 && (
          <div onClick={() => handleMore('tv')}>{tvCount > 20 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}</div>
        )}
      </>
    );
  }
}

export default Details;
