import React, { useState, useEffect } from 'react';
import {
  ItemWrapper,
  Item,
  TitleImg,
  MovieInfo,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  GoBack,
  Sec,
  SeeMore,
  Posters,
  PosterWrapper,
  CenterTitle,
  PhotoGallery,
  OutWrp,
  CloseButton,
  OpenFull,
} from '../styles/searchstyles';
import Grid from '@material-ui/core/Grid';
import EmptyPoster from '../../img/empty_poster.jpg';
import EmptyActor from '../../img/empty_actor.jpg';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import CloseIcon from '@material-ui/icons/Close';
// import Draggable from 'react-draggable';

function Detailsmovie(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [isLoaded3, setIsLoaded3] = useState(false);
  const [item, setItem] = useState('');
  const [credits, setCredits] = useState('');
  const [actorsCount, setActorsCount] = useState(18);
  const [fanarts, setFanarts] = useState('');
  const [art1, setArt1] = useState(6);
  const [art2, setArt2] = useState(6);
  const [art3, setArt3] = useState(6);
  const [art4, setArt4] = useState(6);
  const [art5, setArt5] = useState(6);
  const [art6, setArt6] = useState(6);
  const [art7, setArt7] = useState(6);
  const [art8, setArt8] = useState(6);
  const [galery, setGalery] = useState(false);
  const [modalImg, setModalImg] = useState(false);
  const api_key = '58666352553834b5820ef436680fb2e5';
  const fanart_api_key = '38652d5fb207e88841ba44aa862c86b5';
  const movie_id = props.match.params.id;
  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPoster} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActor} />;
      }
    }
  };
  const handleMoreImg = (a) => {
    switch (a) {
      case 1:
        if (art1 === 1000) setArt1(6);
        else setArt1(1000);
        break;
      case 2:
        if (art2 === 1000) setArt2(6);
        else setArt2(1000);
        break;
      case 3:
        if (art3 === 1000) setArt3(6);
        else setArt3(1000);
        break;
      case 4:
        if (art4 === 1000) setArt4(6);
        else setArt4(1000);
        break;
      case 5:
        if (art5 === 1000) setArt5(6);
        else setArt5(1000);
        break;
      case 6:
        if (art6 === 1000) setArt6(6);
        else setArt6(1000);
        break;
      case 7:
        if (art7 === 1000) setArt7(6);
        else setArt7(1000);
        break;
      case 8:
        if (art8 === 1000) setArt8(6);
        else setArt8(1000);
        break;
      default:
        break;
    }
  };
  const handleMore = () => {
    if (actorsCount === 1000) setActorsCount(18);
    else setActorsCount(1000);
  };
  const handleOpen = (src) => {
    setModalImg(src);
    setGalery(true);
  };
  const handleClose = () => {
    setGalery(false);
  };
  useEffect(() => {
    fetch(`https://api.themoviedb.org/3/movie/${movie_id}?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          result.media_type = 'movie';
          setItem(result);
          setIsLoaded(true);
          getFanarts(result.imdb_id);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/movie/${movie_id}/credits?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setCredits(result);
          setIsLoaded2(true);
        },
        (error) => {
          setIsLoaded2(true);
          setError(error);
        }
      );
    const getFanarts = (id) => {
      const imdb_id = id;
      fetch(`http://webservice.fanart.tv/v3/movies/${imdb_id}?api_key=${fanart_api_key}`)
        .then((res) => res.json())
        .then(
          (result) => {
            setFanarts(result);
            setIsLoaded3(true);
          },
          (error) => {
            setIsLoaded3(true);
            setError(error);
          }
        );
    };
    window.scrollTo(0, 0);
  }, []);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded || !isLoaded2 || !isLoaded3) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        {/* <PhotoGallery disableAutoFocus={true} open={galery} onClose={handleClose}>
          <div style={{ position: 'relative', backgroundColor: 'rgb(250, 252, 255)' }}>
            <img src={modalImg} />
            <CloseButton onClick={handleClose}>
              <CloseIcon fontSize='large' />
            </CloseButton>
          </div>
        </PhotoGallery> */}
        <PhotoGallery maxWidth='xs' fullWidth={true} open={galery} onClose={handleClose}>
          <img alt='Modal' src={modalImg} style={{ maxWidth: '100%', maxHeight: '100%' }} />
          <CloseButton onClick={handleClose}>
            <CloseIcon fontSize='large' />
          </CloseButton>
          <OpenFull target='_blank' href={modalImg}>
            Otwórz w pełnej rozdzielczości
          </OpenFull>
        </PhotoGallery>
        <Grid container spacing={0} style={{ postition: 'relative' }}>
          <GoBack>
            <ArrowBackIcon fontSize='large' onClick={() => props.history.goBack()} />
          </GoBack>
          <PhotoContainer item xs={12} md={6} lg={4}>
            {havePhoto(item, 'big')}
            <TitleItem>{item.title}</TitleItem>
            {` ( ${item.release_date.slice(0, 4)} )`}
          </PhotoContainer>
          <InfoContainer item xs={12} md={6} xl={8}>
            <MovieInfo>
              Typ: <span>{item.media_type}</span>
            </MovieInfo>{' '}
            <MovieInfo>
              Premiera: <span>{item.release_date}</span>
            </MovieInfo>{' '}
            <MovieInfo>
              Gatunek: <span>{item.genres.map((i) => i.name).join(', ')}</span>
            </MovieInfo>
            <MovieInfo>
              Status: <span>{item.status}</span>
            </MovieInfo>
            <MovieInfo>
              Czas trwania: <span>{item.runtime} minut</span>
            </MovieInfo>{' '}
            <MovieInfo>
              Język: <span>{item.spoken_languages.map((i) => i.name).join(', ')}</span>
            </MovieInfo>
            <MovieInfo>
              {item.production_countries.length > 1 ? 'Kraje: ' : 'Kraj: '}
              <span>{item.production_countries.map((i) => i.name).join(', ')}</span>
            </MovieInfo>
            <MovieInfo>
              {item.production_companies.length > 1 ? 'Wytwórnie: ' : 'Wytwórnia: '}
              <span>{item.production_companies.map((i) => i.name).join(', ')}</span>
            </MovieInfo>
            {item.tagline && (
              <MovieInfo>
                Slogan: <span>{item.tagline}</span>
              </MovieInfo>
            )}
            <MovieInfo style={{ marginTop: '30px' }}>
              Opis: <span>{item.overview ? item.overview : 'Brak polskiego opisu'}</span>
            </MovieInfo>
          </InfoContainer>
        </Grid>
        <Sec>Obsada:</Sec>
        <ItemWrapper>
          {credits.cast.slice(0, actorsCount).map((person, index) => {
            return (
              <Item to={`/infcinema/details/person/${person.id}`} key={index}>
                {havePhoto(person)}
                <div className='tit2'>
                  {person.name}
                  <br />
                  <span className='character'>{person.character}</span>
                </div>
              </Item>
            );
          })}
        </ItemWrapper>
        {credits.cast.length > 18 && (
          <div onClick={handleMore}>{actorsCount > 20 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}</div>
        )}
        {fanarts.status !== 'error' && <Sec style={{ marginBottom: '30px', marginTop: '120px' }}>Grafiki:</Sec>}

        {fanarts.hdmovieclearart && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              HD ClearArt
            </CenterTitle>
            {fanarts.hdmovieclearart.slice(0, art1).map((im, index) => (
              <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                <Posters onClick={() => handleOpen(im.url)} src={im.url} alt={im.id} />
              </PosterWrapper>
            ))}
            {fanarts.hdmovieclearart.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(1)}>
                {art1 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}
          </OutWrp>
        )}
        {fanarts.hdmovielogo && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              HD Logo
            </CenterTitle>

            {fanarts.hdmovielogo.slice(0, art2).map((im, index) => (
              <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                <Posters onClick={() => handleOpen(im.url)} src={im.url} alt={im.id} />
              </PosterWrapper>
            ))}
            {fanarts.hdmovielogo.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(2)}>
                {art2 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}
          </OutWrp>
        )}
        {fanarts.moviebackground && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              Tło
            </CenterTitle>

            {fanarts.moviebackground.slice(0, art3).map((im, index) => (
              <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                <Posters onClick={() => handleOpen(im.url)} src={im.url} alt={im.id} />
              </PosterWrapper>
            ))}
            {fanarts.moviebackground.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(3)}>
                {art3 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}
          </OutWrp>
        )}
        {fanarts.moviebanner && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              Banner
            </CenterTitle>

            {fanarts.moviebanner.slice(0, art4).map((im, index) => (
              <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                <Posters onClick={() => handleOpen(im.url)} src={im.url} alt={im.id} />
              </PosterWrapper>
            ))}
            {fanarts.moviebanner.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(4)}>
                {art4 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}
          </OutWrp>
        )}
        {fanarts.moviedisc && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              Okładka na płytę
            </CenterTitle>

            {fanarts.moviedisc.slice(0, art5).map((im, index) => (
              <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                <Posters onClick={() => handleOpen(im.url)} src={im.url} alt={im.id} />
              </PosterWrapper>
            ))}
            {fanarts.moviedisc.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(5)}>
                {art5 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}
          </OutWrp>
        )}
        {fanarts.movielogo && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              Logo
            </CenterTitle>

            {fanarts.movielogo.slice(0, art6).map((im, index) => (
              <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                <Posters onClick={() => handleOpen(im.url)} src={im.url} alt={im.id} />
              </PosterWrapper>
            ))}
            {fanarts.movielogo.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(6)}>
                {art6 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}
          </OutWrp>
        )}
        {fanarts.movieposter && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              Plakat
            </CenterTitle>

            {fanarts.movieposter.slice(0, art7).map((im, index) => (
              <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                <Posters onClick={() => handleOpen(im.url)} src={im.url} alt={im.id} />
              </PosterWrapper>
            ))}
            {fanarts.movieposter.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(7)}>
                {art7 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}
          </OutWrp>
        )}
        {fanarts.moviethumb && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              Tło
            </CenterTitle>

            {fanarts.moviethumb.slice(0, art8).map((im, index) => (
              <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                <Posters onClick={() => handleOpen(im.url)} src={im.url} alt={im.id} />
              </PosterWrapper>
            ))}
            {fanarts.moviethumb.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(8)}>
                {art8 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}
          </OutWrp>
        )}
      </>
    );
  }
}

export default Detailsmovie;
