import React from 'react';
import { Route } from 'react-router-dom';
import Search from './components/search/search';
import Detailsmovie from './components/detailsmovie/detailsmovie';
import Detailstv from './components/detailstv/detailstv';
import Detailsperson from './components/detailsperson/detailsperson';
import Detailsseason from './components/detailsseason/detailsseason';
import Footer from './components/footer/footer';
import Nav from './components/nav/nav';

function App() {
  return (
    <>
      <Route path='/infcinema' component={Nav} />
      <Route exact path='/infcinema/' component={Search} />
      <Route exact path='/infcinema/details/movie/:id' component={Detailsmovie} />
      <Route exact path='/infcinema/details/tv/:id' component={Detailstv} />
      <Route exact path='/infcinema/details/tv/:id/season/:idseason' component={Detailsseason} />
      <Route exact path='/infcinema/details/person/:id' component={Detailsperson} />
      <Footer />
    </>
  );
}

export default App;
